import React, { useState } from "react";

const Part1 = ({ setImage }) => {
  const [plugin, setPlugin] = useState(true);
  const [theme, setTheme] = useState(false);

  const handlePluginClick = () => {
    setPlugin(true);
    setTheme(false);
    setImage(true);
  };

  const handleThemeClick = () => {
    setPlugin(false);
    setTheme(true);
    setImage(false);
  };

  return (
    <>
      <div className="row">
        <div className="col-sm-12">
          <div className="row items-center">
            <div className="col-auto pr-0">
              <img
                src="//images.ctfassets.net/vkdbses00qqt/h7RP1nspwFDZsZEGdGY4n/07bff085583e644a70c1c81a85d21c88/plugins-icon.svg"
                alt=""
                loading="lazy"
              />
            </div>
            <div className="col pl-0">
              <h3 className="pl-4">
                <button aria-expanded="true" class="css-11zrbhq">
                  <div className="row">
                    <div
                      onClick={handlePluginClick}
                      className="col-sm-12 flex justify-left items-center "
                    >
                      <span
                        onClick={handlePluginClick}
                        className="text-3xl  font-bold"
                      >
                        Plugins
                      </span>
                      <br />
                    </div>
                  </div>
                </button>
              </h3>
            </div>
            <div className="col-auto">
              <button
                onClick={handlePluginClick}
                style={{
                  transform: plugin === true ? "rotate(180deg)" : null,
                }}
              >
                <svg
                  className={`w-8 h-8 text-grey-colour rounded-lg ${
                    plugin === true ? "bg-primary-2 text-primary-dark" : null
                  }`}
                  stroke="currentColor"
                  fill={`${
                    plugin === true ? "text-primary-dark" : "currentColor"
                  }`}
                  stroke-width="0"
                  viewBox="0 0 24 24"
                  height="1em"
                  width="1em"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path d="M16.59 8.59L12 13.17 7.41 8.59 6 10l6 6 6-6z"></path>
                </svg>
              </button>
            </div>
          </div>

          <div className="row text-left pl-12 mt-6">
            {plugin === true ? (
              <>
                <p style={{ fontSize: "18px" }}>
                  Plugins are packages that extend Gatsby sites. They can source
                  content, transform data, and more. Anything you can imagine 
                  from CMSs to e-commerce  has a Gatsby plugin for seamless
                  integration of services.
                </p>
                <div className="row">
                  <div className="col-md-6">
                    <button
                      type="submit"
                      className="flex justify-center items-center  my-8 py-3 px-5 border border-transparent text-md font-medium rounded-md text-white bg-primary-dark hover:bg-primary-darkest focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                    >
                      <p className="text-xl">Browse Plugins</p>
                      <svg
                        className="w-5 h-5"
                        stroke="currentColor"
                        fill="currentColor"
                        stroke-width="0"
                        viewBox="0 0 24 24"
                        height="1em"
                        width="1em"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"></path>
                      </svg>
                    </button>
                  </div>
                </div>
              </>
            ) : null}
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-md-12">
          <hr className="text-gray-400" />
        </div>
      </div>
      <div className="row mt-6">
        <div className="col-sm-12">
          <div className="row items-center">
            <div className="col-auto pr-0">
              <img
                src="https://images.ctfassets.net/vkdbses00qqt/6XaDibywAm2S2ySY81BbaW/4809389d4e2e0ccf31228c0a12ec7d2f/themes-icon.svg"
                alt=""
                loading="lazy"
                class="css-x6y73b"
              />
            </div>
            <div className="col pl-0">
              <h3 className="pl-4">
                <button aria-expanded="true" class="css-11zrbhq">
                  <div className="row">
                    <div
                      onClick={handleThemeClick}
                      className="col-sm-12  flex justify-left items-center"
                    >
                      <span
                        onClick={handleThemeClick}
                        className="text-3xl  font-bold"
                      >
                        Themes
                      </span>
                      <br />
                    </div>
                  </div>
                </button>
              </h3>
            </div>
            <div className="col-auto">
              <button
                onClick={handleThemeClick}
                style={{
                  transform: theme === true ? "rotate(180deg)" : null,
                }}
              >
                <svg
                  className={`w-8 h-8 text-grey-colour rounded-lg ${
                    theme === true ? "bg-primary-2 text-primary-dark" : null
                  }`}
                  stroke="currentColor"
                  fill={`${
                    theme === true ? "text-primary-dark" : "currentColor"
                  }`}
                  stroke-width="0"
                  viewBox="0 0 24 24"
                  height="1em"
                  width="1em"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path d="M16.59 8.59L12 13.17 7.41 8.59 6 10l6 6 6-6z"></path>
                </svg>
              </button>
            </div>
          </div>

          <div className="row text-left pl-12 mt-6">
            {theme === true ? (
              <>
                <p style={{ fontSize: "18px" }}>
                  Themes wrap all of your default configuration into a single
                  package, installed as a dependency so your sites
                  functionality can be versioned and centrally managed for easy
                  updating. Compose themes together, and even swap out one
                  compatible theme for another.
                </p>
                <div className="row">
                  <div className="col-md-6">
                    <button
                      type="submit"
                      className="flex justify-center items-center  my-8 py-3 px-5 border border-transparent text-md font-medium rounded-md text-white bg-primary-dark hover:bg-primary-darkest focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                    >
                      <p className="text-xl">Browse Themes</p>
                      <svg
                        className="w-5 h-5"
                        stroke="currentColor"
                        fill="currentColor"
                        stroke-width="0"
                        viewBox="0 0 24 24"
                        height="1em"
                        width="1em"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"></path>
                      </svg>
                    </button>
                  </div>
                </div>
              </>
            ) : null}
          </div>
        </div>
      </div>
    </>
  );
};

export default Part1;
