import React from "react";

const KundaliForm = () => {
  return (
    <>
      <div className="flex mt-6 flex-row ">
        <div className="flex flex-col">
          <div className="grid grid-cols-1">
            <label>Name</label>
            <input
              className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
              type="text"
              placeholder="Name"
            />
          </div>

          <div className="grid grid-cols-3 gap-8 mt-4">
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>Date of Birth</option>
            </select>
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>Month</option>
            </select>
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>Year</option>
            </select>
          </div>
          <div className="grid grid-cols-3 gap-8 mt-4">
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>Time of Birth</option>
            </select>
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>Minute</option>
            </select>
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>Seconds</option>
            </select>
          </div>
          <div className="grid grid-cols-3 gap-8 mt-4">
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>India</option>
            </select>
            <input
              type="text"
              className="px-3 py-2 col-span-2  leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
              placeholder="Hyderabad, Telangana, India"
            />
          </div>
          <div className="grid grid-cols-2 gap-8 mt-4">
            <input
              type="email"
              className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
              placeholder="Email"
            />
            <input
              type="text"
              className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
              placeholder="Phone Number"
            />
          </div>
          <div className="grid grid-cols-2 gap-8 mt-4">
            <label>Language</label>
            <label>Chart Type</label>
          </div>
          <div className="grid grid-cols-2 gap-8">
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>English</option>
            </select>
            <select className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400">
              <option>South Indian Style</option>
            </select>
          </div>
          <div className="grid grid-cols-1 gap-8 mt-4">
            <button
              className="px-3 py-2 leading-5 border rounded-4px bg-form-1 hover:bg-form-hover-button"
              type="button"
            >
              <span className="text-white">GENERATE HOROSCOPE</span>
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default KundaliForm;
