import React, { useState } from "react";
import PluginPic from "../../Assets/Images/plugins.webp";
import ThemePic from "../../Assets/Images/themes.webp";
import Part1 from "./Part1";

const Section12 = () => {
  const [image, setImage] = useState(true);

  return (
    <div className="css-1igbuhm mt-10">
      <div className="row">
        <div className="col-lg-6">
          <Part1 setImage={setImage} />
        </div>
        {image === true ? (
          <div className="col-lg-6 hidden lg:block">
            <img src={PluginPic} alt="pluginPic" />
          </div>
        ) : (
          <div className="col-lg-6 hidden lg:block">
            <img src={ThemePic} alt="themePic" />
          </div>
        )}
      </div>
    </div>
  );
};

export default Section12;
