import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Images from "./Pages/Page1/Images";
import Page1 from "./Pages/Page1/Index";
import Main from "./Components/Main/Main";

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path="/">
            <Page1 />
          </Route>
          <Route exact path="/main">
            <Main />
          </Route>

          <Route exact path="/docs/tutorial/part/:v">
            <Page1 />
          </Route>

          <Route exact path="/image">
            <Images />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
