import React, { useState } from "react";

const MatchingForm1 = () => {
  const [matchform1, setMatchform1] = useState(true);

  const [values, setValues] = useState({
    groomName: "",
    date: "",
    month: "",
    year: "",
    hour: "",
    minute: "",
    second: "",
    country: "",
    birthPlace: "",
    language: "",
    chartType: "",
  });
  const [values2, setValues2] = useState({
    brideName: "",
    date: "",
    month: "",
    year: "",
    hour: "",
    minute: "",
    second: "",
    country: "",
    birthPlace: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setValues({
      ...values,
      [name]: value,
    });
  };

  const handleChange2 = (e) => {
    const { name, value } = e.target;
    setValues2({
      ...values2,
      [name]: value,
    });
  };

  const handleNextClick = () => {
    setMatchform1(false);
  };
  const handleBackSubmit = () => {
    setMatchform1(true);
  };
  return (
    <>
      <div className="flex flex-row">
        {matchform1 === true ? (
          <div className="flex flex-col">
            <div className="mt-4">
              <h6>Enter groom birth details</h6>
            </div>
            <div className="grid grid-cols-1 mt-4">
              <label>Name</label>
              <input
                onChange={handleChange}
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                type="text"
                placeholder="Groom Name"
                value={values.groomName}
                name="groomName"
              />
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <label>Date</label>
              <label>Month</label>
              <label>Year</label>
            </div>
            <div className="grid grid-cols-3 gap-8">
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="Date"
                value={values.date}
                name="date"
                onChange={handleChange}
              />
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="Month"
                value={values.month}
                name="month"
                onChange={handleChange}
              />
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="Year"
                value={values.year}
                name="year"
                onChange={handleChange}
              />
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <label>Hour</label>
              <label>Minute</label>
              <label>Second</label>
            </div>
            <div className="grid grid-cols-3 gap-8">
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="HH"
                name="hour"
                onChange={handleChange}
                value={values.hour}
              />
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="MM"
                name="minute"
                onChange={handleChange}
                value={values.minute}
              />
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="SS"
                name="second"
                onChange={handleChange}
                value={values.second}
              />
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <label>Country</label>
              <label className="col-span-2">
                Birth place (
                <span className="text-purple-900">+Advanced Option</span>)
              </label>
            </div>
            <div className="grid grid-cols-3 gap-8">
              <input
                type="text"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="India"
                name="country"
                onChange={handleChange}
                value={values.country}
              />
              <input
                type="text"
                className="px-3 py-2 col-span-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="vadodara"
                name="birthPlace"
                onChange={handleChange}
                value={values.birthPlace}
              />
            </div>
            <div className="grid grid-cols-2 gap-8 mt-4">
              <label>Language</label>
              <label>Chart Type</label>
            </div>
            <div className="grid grid-cols-2 gap-8">
              <select
                name="language"
                value={values.language}
                onChange={handleChange}
                className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
              >
                <option value="english">English</option>
                <option value="hindi">Hindi</option>
              </select>
              <select
                name="chartType"
                value={values.chartType}
                onChange={handleChange}
                className="px-2 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
              >
                <option value="south">South Indian Style</option>
                <option value="north">North Indian Style</option>
              </select>
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <label className="col-span-2">
                * Enter bride details on next step
              </label>
              <button
                onClick={handleNextClick}
                className="px-8 py-2 leading-5 border rounded-4px bg-form-1 hover:bg-form-hover-button"
                type="button"
              >
                <span className="text-white">NEXT</span>
              </button>
            </div>
          </div>
        ) : (
          <div className="flex flex-col">
            <div className="mt-4">
              <h6>Enter bride birth details</h6>
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <label>Name</label>
            </div>
            <div className="grid grid-cols-1 gap-8">
              <input
                className="px-3  py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                type="text"
                placeholder="Bride Name"
                value={values2.brideName}
                name="brideName"
                onChange={handleChange2}
              />
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <label>Date</label>
              <label>Month</label>
              <label>Year</label>
            </div>
            <div className="grid grid-cols-3 gap-8">
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="Date"
                value={values2.date}
                name="date"
                onChange={handleChange2}
              />
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="Month"
                value={values2.month}
                name="month"
                onChange={handleChange2}
              />
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="Year"
                value={values2.year}
                name="year"
                onChange={handleChange2}
              />
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <label>Hour</label>
              <label>Minute</label>
              <label>Second</label>
            </div>
            <div className="grid grid-cols-3 gap-8">
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="HH"
                value={values2.hour}
                name="hour"
                onChange={handleChange2}
              />
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="MM"
                value={values2.minute}
                name="minute"
                onChange={handleChange2}
              />
              <input
                type="number"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="SS"
                value={values2.second}
                name="second"
                onChange={handleChange2}
              />
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <label>Country</label>
              <label className="col-span-2">
                Birth place (
                <span className="text-purple-900">+Advanced Option</span>)
              </label>
            </div>
            <div className="grid grid-cols-3 gap-8 mt-4">
              <input
                type="text"
                className="px-3 py-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="India"
                value={values2.country}
                name="country"
                onChange={handleChange2}
              />
              <input
                type="text"
                className="px-3 py-2 col-span-2 leading-5 border rounded-4px focus:outline-none  focus:border-purple-400"
                placeholder="Enter Your Birth Place"
                value={values2.birthPlace}
                name="birthPlace"
                onChange={handleChange2}
              />
            </div>

            <div className="grid grid-cols-2 gap-8 mt-4">
              <button
                onClick={handleBackSubmit}
                className="px-16 py-2 leading-5 border rounded-4px bg-form-1 hover:bg-form-hover-button"
                type="button"
              >
                <span className="text-white">BACK</span>
              </button>
              <button
                className="px-16 py-2 leading-5 border rounded-4px bg-form-1 hover:bg-form-hover-button"
                type="button"
              >
                <span className="text-white">NEXT</span>
              </button>
            </div>
          </div>
        )}
      </div>
    </>
  );
};

export default MatchingForm1;
