import React, { useState } from "react";
import KundaliForm from "./KundaliForm";
import MatchingForm1 from "./MatchingForm1";

const RadioButton = () => {
  const [value, setValue] = useState("kundali");

  const handleKundaliChange = (e) => {
    setValue(e.target.value);
  };
  const handleMatchingChange = (e) => {
    setValue(e.target.value);
  };
  const handleConsultationChange = (e) => {
    setValue(e.target.value);
  };

  const handleConsultationClick = () => {
    window.location.href = "https://demo.officebrains.in/consult/";
  };

  return (
    <>
      <div className="flex flex-row justify-center">
        <div className="flex w-width flex-col shadow-2xl p-5 rounded-lg">
          <div className="grid grid-cols-3 ">
            <div>
              <input
                style={{
                  background: " #8a4baf !important",
                  outline: "2px solid #dec4ec !important",
                  outlineOffset: "3px !important",
                }}
                className="h-4 w-4 text-form-1"
                type="radio"
                value="kundali"
                onChange={handleKundaliChange}
                name="name"
                checked={value === "kundali" ? "checked" : null}
              />

              <label className="align-text-bottom ml-1 text-font">
                Kundali
              </label>
            </div>
            <div>
              <input
                type="radio"
                className="h-4 w-4 border-gray-300 rounded text-indigo-600 focus:ring-indigo-500"
                value="matching"
                onChange={handleMatchingChange}
                name="name"
              />
              <label className="align-text-bottom ml-1 text-font">
                Matching
              </label>
            </div>
            <div>
              <input
                type="radio"
                className="h-4 w-4 border-gray-300 rounded text-indigo-600 focus:ring-indigo-500"
                value="consultation"
                onChange={handleConsultationChange}
                onClick={handleConsultationClick}
                name="name"
              />

              <label className="align-text-bottom ml-1 text-font">
                Consultation
              </label>
            </div>
          </div>
          <div className="grid grid-cols-1">
            {value === "kundali" || value === "consultation" ? (
              <KundaliForm />
            ) : value === "matching" ? (
              <MatchingForm1 />
            ) : null}
          </div>
        </div>
      </div>
    </>
  );
};

export default RadioButton;
