import React, { useContext, useState } from "react";
import Image from "../../Assets/Part1/deployment-workflow.png";
import CLI from "../../Assets/Part1/cli.png";
import localhost from "../../Assets/Part1/localhost.png";
import newRepoButton from "../../Assets/Part1/new-repo-button.png";
import newRepoOptions from "../../Assets/Part1/new-repo-options.png";
import siteButton from "../../Assets/Part1/create-a-site-button.png";
import importGitRepo from "../../Assets/Part1/import-a-git-repo.png";
import selectGitRepo from "../../Assets/Part1/select-a-git-provider.png";
import selectDestination from "../../Assets/Part1/select-destination.png";
import gatsByCloudPermission from "../../Assets/Part1/gatsby-cloud-permissions.png";
import selectRepository from "../../Assets/Part1/select-repository.png";
import addSiteDetails from "../../Assets/Part1/add-site-details.png";
import optionalIntegrations from "../../Assets/Part1/optional-integrations.png";
import setup from "../../Assets/Part1/setup.png";
import sitePage from "../../Assets/Part1/site-page.png";
import globalContext from "../../Components/context/globalContext";

const Images = () => {
  const { imageValue } = useContext(globalContext);
  const [zoom, setZoom] = useState(false);

  return (
    <div className="bg-black pt-40 flex justify-center items-center">
      <img
        className={
          zoom === false
            ? "w-9/12  cursor-zoom-in"
            : "cursor-zoom-out w-10/12 m-0 align-middle "
        }
        src={
          imageValue === 1
            ? Image
            : imageValue === 2
            ? CLI
            : imageValue === 3
            ? localhost
            : imageValue === 4
            ? newRepoButton
            : imageValue === 5
            ? newRepoOptions
            : imageValue === 6
            ? siteButton
            : imageValue === 7
            ? importGitRepo
            : imageValue === 8
            ? selectGitRepo
            : imageValue === 9
            ? selectDestination
            : imageValue === 10
            ? gatsByCloudPermission
            : imageValue === 11
            ? selectRepository
            : imageValue === 12
            ? addSiteDetails
            : imageValue === 13
            ? optionalIntegrations
            : imageValue === 14
            ? setup
            : imageValue === 15
            ? sitePage
            : null
        }
        onClick={() => (zoom === false ? setZoom(true) : setZoom(false))}
      />
    </div>
  );
};

export default Images;
