import React, { useState } from "react";
import ItemsCarousel from "react-items-carousel";

const Carousel = () => {
  const [activeItemIndex, setActiveItemIndex] = useState(0);
  const chevronWidth = 10;
  return (
    <div style={{ padding: `0 ${chevronWidth}px` }}>
      <ItemsCarousel
        requestToChangeActive={setActiveItemIndex}
        activeItemIndex={activeItemIndex}
        numberOfCards={2}
        gutter={20}
        leftChevron={<button>{"<"}</button>}
        rightChevron={<button>{">"}</button>}
        outsideChevron
        chevronWidth={chevronWidth}
        infiniteLoop="true"
      >
        <div className="runing-animation">
          <picture>
            <source
              type="image/webp"
              srcset="https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=1731&amp;h=24&amp;q=80&amp;fm=webp&amp;fit=fill 1x,
https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=2597&amp;h=36&amp;q=80&amp;fm=webp&amp;fit=fill 1.5x,
https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=3462&amp;h=48&amp;q=80&amp;fm=webp&amp;fit=fill 2x"
            />
            <source
              srcset="https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=1731&amp;h=24&amp;q=80&amp;fit=fill 1x,
https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=2597&amp;h=36&amp;q=80&amp;fit=fill 1.5x,
https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=3462&amp;h=48&amp;q=80&amp;fit=fill 2x"
            />
            <img
              srcset="https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=1731&amp;h=24&amp;q=80&amp;fit=fill 1x,
https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=2597&amp;h=36&amp;q=80&amp;fit=fill 1.5x,
https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=3462&amp;h=48&amp;q=80&amp;fit=fill 2x"
              src="https://images.ctfassets.net/vkdbses00qqt/6gYYhIZTJq0VHl3tXT8oPC/18d49aaa1fd3fed25b6b6593350897b7/homepage-used-by-logos__2_.png?w=1731&amp;h=24&amp;q=80&amp;fit=fill"
              alt="Logos for Airbnb, Meetup, TypeScript, Fabric, Segment, Apollo, Hopper, SendGrid, PayPal, Flamingo, React, Formidable, IBM, Braun, Spotify, Little Caesars, and Figma"
              width="1731"
              height="250"
              loading="eager"
            />
          </picture>
        </div>
      </ItemsCarousel>
    </div>
  );
};

export default Carousel;
